div.supapress {
  margin: 0;
  overflow: hidden;
  padding: 0;
}

div.supapress div[id^="isbn-grid-"] .book-wrapper,
div.supapress div[id^="search-grid-"] .book-wrapper {
  padding: 10px;
  text-align: center;
}

div.supapress div[id^="isbn-grid-"] div.image-wrapper,
div.supapress div[id^="isbn-carousel-"] div.image-wrapper,
div.supapress div[id^="search-grid-"] div.image-wrapper {
  position: relative;
}

div.supapress div[id^="isbn-carousel-"] div.image-wrapper {
  width: 100%;
}

div.supapress div[id^="isbn-list-"] div.image-wrapper,
div.supapress div[id^="isbn-list-"] div.information-wrapper,
div.supapress div[id^="product-details-"] div.image-wrapper,
div.supapress div[id^="product-details-"] div.information-wrapper,
div.supapress div[id^="search-list-"] div.image-wrapper,
div.supapress div[id^="search-list-"] div.information-wrapper {
  float: left;
  padding: 10px;
  max-width: 50%;
}

div.supapress div[id^="product-details-"] div.title-wrapper {
  padding: 10px;
}

div.supapress div[id^="isbn-list-"] .right div.image-wrapper,
div.supapress div[id^="search-list-"] .right div.image-wrapper {
  float: right;
}

div.supapress div[id^="isbn-list-"] .book-wrapper img,
div.supapress div[id^="search-list-"] .book-wrapper img {
  max-height: 285px;
}

div.supapress div[id^="isbn-grid-"] .book-wrapper img,
div.supapress div[id^="isbn-carousel-"] .book-wrapper img,
div.supapress div[id^="search-grid-"] .book-wrapper img {
  height: auto;
  margin: 0 auto;
  max-width: 100%;
  width: 100%;
}

div.supapress div[id^="isbn-grid-"] .book-wrapper img.baselined,
div.supapress div[id^="isbn-carousel-"] .book-wrapper img.baselined,
div.supapress div[id^="search-grid-"] .book-wrapper img.baselined {
  bottom: 0;
  left: 0;
  position: absolute;
}

div.supapress .book-wrapper {
  float: left;
  position: relative;
  box-sizing: border-box;
}

div.supapress div[id^="isbn-carousel-"] .book-wrapper {
  padding: 10px;
}

div.supapress div[id^="isbn-carousel-"] .slick-prev::before,
div.supapress div[id^="isbn-carousel-"] .slick-next::before {
  color: #000000;
}