div.supapress {
  .search-wrapper {
    overflow: hidden;
    width: 100%;

    &.has-filters {
      display: inline-block;
      float: left;
      width: 65%;
    }
  }

  .pagination-wrapper {
    padding: 10px;

    p {
      margin: 0;
    }

    .supapress-pagination-wrapper {
      a, span {
        padding: 0 5px;
      }

      a {
        border: none;
      }

      span {
        color: #DD3D36;
      }
    }
  }

  /**
  For backwards compatibility.
  Styles moved to id but should still be on .filter-wrapper if it is parent of id^=filter-wrapper
  */
  .filter-wrapper {
    display: inline-block;
    float: left;
    max-width: 250px;
    padding: 10px;
    width: 35%;

    [id^=filter-wrapper] {
      display: unset;
      float: unset;
      max-width: unset;
      padding: unset;
      width: unset;
    }
  }

  [id^=filter-wrapper] {
    display: inline-block;
    float: left;
    max-width: 250px;
    padding: 10px;
    width: 35%;

    .filter-load-wrapper {
      background: #E3E3E3;
      display: block;
      padding: 20px;

      &.preloader-on {
        text-align: center;

        img {
          display: block;
          margin: auto;
        }
      }
    }

    .filter-block {
      border-bottom: 2px solid #CCCAC7;
      display: block;
      margin-bottom: 15px;
      padding-bottom: 15px;

      &:last-child {
        border: none;
        margin: 0;
        padding: 0;
      }
    }

    h3 {
      font-size: 18px;
      margin: 0 0 8px;
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        font-size: 14px;

        a {
          border: none;
        }
      }

      .additional-filters {
        -webkit-transition: max-height 0.3s ease-in;
        -moz-transition: max-height 0.3s ease-in;
        transition: max-height 0.3s ease-in;
        display: block;
        overflow: hidden;
      }

      .additional-filters-toggle {
        font-size: 14px;

        &:focus, &:active, &:visited {
          border: none;
          outline: none;
        }
      }
    }

    .clear-filter {
      color: #DD3D36;
    }
  }
}

@media screen and (max-width: 480px) {
  div.supapress {
    .search-wrapper.has-filters,
    .filter-wrapper {
      display: block;
      width: 100%;
    }

    .filter-wrapper {
      max-width: initial;
    }

    .supapress-sort-by-wrapper,
    .supapress-per-page-wrapper,
    .supapress-pagination-wrapper {
      display: block;
      text-align: center;
      width: 100%;

      &.supapress-per-page-wrapper {
        margin: 10px 0;
      }

      select {
        width: 100%;
      }
    }
  }
}